import React, { useState } from "react";
import { css } from '@emotion/css';
import { Button, Modal, ButtonGroup } from "react-bootstrap";
import { Main } from "./Main";
interface Props {
    img: string
    setImg: (img: string) => void,
    aspect: number
}
export function CropModal({ img, setImg, aspect }: Props) {
    const [open, setOpen] = useState(true);
    return <ButtonGroup>
        <Modal Modal className={css`
        &> .modal-dialog{
            margin: 0 0 0 0;
            padding: 0 0 0 0;
            width:100vw;
            height:100vh;
            max-width: 100vw;
        }
        &> .modal-dialog> .modal-content{
            width:100vw;
            height:100vh;
        }`} animation show={open} onHide={() => setOpen(false)}>
            <Modal.Header closeButton>
                CORTAR IMAGEN
            </Modal.Header>
            <Modal.Body>
                <Main img={img} setImg={(s) => { setImg(s); setOpen(false) }} aspect={aspect} />
            </Modal.Body>
        </Modal>
        <Button onClick={() => setOpen(true)}>
            cortar
        </Button>
    </ButtonGroup>
}