import react, { useContext } from 'react';
import { content, imagen } from './props';
import SlateProvider, { EditArea } from '../../../SlateCustom';
import HoveringToolbar from '../../../SlateCustom/hoverMenu';
import { iten, State } from '../index';
import { Carousel } from 'react-bootstrap';

export function Itens({
    config: { margin, padding, animated, seleted, text, opacidad, font },
    iten: { id, html, img, col, row } }: { iten: iten, config: State }) {
    return <div className='iten'>
        <video autoPlay loop muted width='100%' poster={img}>
        <img style={imagen} alt="Bricks" src={img} />
            <source src={img} type="video/mp4"></source>
        </video>
        <Carousel.Caption  key={'html' + id} dangerouslySetInnerHTML={{ __html: html }} />
        {/* <Carousel.Caption><EditArea key={'edit' + id} /></Carousel.Caption> */}
    </div>
}