import react, { useState } from 'react';
import { getCSS } from './props';
import { Row, Container, Carousel } from 'react-bootstrap';
import { State } from './index'
import { Itens } from './Itens'

export function Panel({ config, setText, seled }: { config: State, setText: (d: any) => void, seled: (i: number) => void }) {
    const { color, itens, width, gap, seleted } = config;
    const prev = seleted === 1 ? itens.length - 1 : seleted - 2;
    const sele = seleted - 1;
    const next = seleted === itens.length ? 0 : seleted;
    const [trans, setTrans] = useState('');
    const setPrev = () => {
        setTrans(' prev');
        setTimeout(() => { setTrans(''); seled(prev + 1) }, 250)
    };
    const setNext = () => {
        setTrans(' next');
        setTimeout(() => { setTrans(''); seled(next + 1) }, 250)
    };
    return <Row style={{ backgroundColor: color, margin: '0 0 0 0', padding: '0 0 0 0' }}>
        <div style={{ aspectRatio: '16/7', width: '100%' }} className={"carousel slide " + getCSS()}>
            <ol className="carousel-indicators">
                {itens.map((i) => <li className={seleted === i.id ? 'active' : ''}></li>)}
            </ol>
            <div className={"carousel-inner" + trans}>
                <Itens key={'iten' + prev} iten={itens[prev]} config={config} />
                <Itens key={'iten' + sele} iten={itens[sele]} config={config} />
                <Itens key={'iten' + next} iten={itens[next]} config={config} />
            </div>
            <div className="carousel-control-prev" role="button" onClick={setPrev}>
                <span aria-hidden="true" className="carousel-control-prev-icon"></span>
                <span className="sr-only">Previous</span>
            </div>
            <div className="carousel-control-next" role="button" onClick={setNext}>
                <span aria-hidden="true" className="carousel-control-next-icon"></span>
                <span className="sr-only">Next</span>
            </div>
        </div>
    </Row >
}
