import { css } from '@emotion/css';
export function getCSS() {
    return css`
    width: 100%;
    overflow: hidden;
    &>.carousel-inner{
        width: 300%;
        margin-left: -100%;
    }
    &> .carousel-inner> .iten{
        position: relative;
        display: inline-block;
        width: 33.333333%; 
        height: 100%; 
        overflow: hidden;
    }
    &> .carousel-inner.prev> .iten,
    &> .carousel-inner.next> .iten{
        transition: all 0.25s;
    }
    &> .carousel-inner.prev> .iten{
        transform: translateX(100%);
    }
    &> .carousel-inner.next> .iten{
        transition: all 0.25s;
        transform: translateX(-100%);
    }`;
}

