import React from "react";
import { Config, Zoom } from './Config'
import { EasyCrop } from './Crop'
import { css } from '@emotion/css';
import getImageManager, { ImageManager, Crop as CropData, MatrizReturn, MatrizNull } from './ImageManager'
interface Props {
    img: string
    setImg: (img: string) => void
    aspect: number
}
export interface State extends MatrizReturn {
    preview: string,
    edit: 'crop' | 'filter' | 'color' | 'contrast' | 'none',
    rotate: number
    zoom: number
    cropData: CropData
}
export class Main extends React.Component<Props, State> {
    img: ImageManager;
    history: string[] = [];
    constructor(p: Props) {
        super(p);
        this.img = getImageManager();
        this.state = {
            edit: 'crop', preview: p.img,
            rotate: 0, zoom: 0, ...MatrizNull,
            cropData: { x: 0, y: 0, width: 0, height: 0 }
        };
    }
    save = async () => { }
    cropImage = async () => {
        const { cropData, rotate, preview } = this.state;
        this.setState({ rotate: 0, zoom: 0, preview: await this.img.cropImage(preview, cropData, rotate) });
    }
    render() {
        const { width, height } = this.state;
        return <div className='d-flex' style={{ width: '100vw', }}>
            <Config draw={() => { }} crop={this.cropImage} save={this.save} config={this.state} setConfig={this.setState.bind(this)} />
            <div className="flex-grow-1" style={{ height: 'calc(100vh - 90px)', border: 'orange 5px solid', overflow: 'auto' }}>
                {this.state.edit === 'crop' ?
                    <div className={css`&> .reactEasyCrop_Container{position: absolute;top: 21px;left: 168px;right: 51px;bottom: 21px;}`}
                        style={{ height: '100%', aspectRatio: (width / height) + '/1', margin: '0 auto 0 auto' }}>
                        <EasyCrop config={this.state} aspect={this.props.aspect} setConfig={this.setState.bind(this)} />
                    </div> :
                    <div className='d-flex h-100' style={{ aspectRatio: (width / height) + '/1', margin: '0 auto 0 auto' }}>
                        <canvas id='canvaImage' className='ml-auto mr-auto' height='100%' />
                    </div>
                }
            </div>
            {this.state.edit === 'crop' ?
                <Zoom value={this.state.zoom} setData={(zoom) => this.setState({ zoom })} /> : <></>
            }
            <div style={{ width: 32 }}></div>
        </div>
    }
}

