import react, { ChangeEvent } from "react";
import { css } from '@emotion/css';
import Slider from '@material-ui/core/Slider'
import { Card, Row, InputGroup, FormControl, Button, ButtonGroup } from "react-bootstrap";
import { CardConfig, RowConfig, ButtonConfig, TitleCard } from '../../Crear/component';
import { State } from '../Main';
import CropEdit, { Zoom } from './CropEdit';
import ColorSeting from './ColorEdit';
import { ImageManager, Crop as CropData } from '../ImageManager'
interface Props {
    config: State;
    setConfig: (d: any) => void,
    save: () => void
    crop: () => void
    draw: () => void
}
export function Config({ config, setConfig, save, crop, draw }: Props) {
    const { edit } = config;
    return <Card className='mr-1' style={{ width: 144, backgroundColor: '#ccc', padding: '7px 7px 7px 7px' }}>
        <Row className='d-flex flex-column' style={{ margin: '0 0 0 0', padding: '0 0 0 0' }}>
            <RowConfig >
                <Guardar guardar={save} />
                <CardConfig width="70">
                    <RowConfig >
                        <ButtonGroup vertical>
                            <ButtonGroup>
                                <ButtonConfig icon='fas fa-check' action={() => setConfig({})} />
                                <ButtonConfig icon='fas fa-trash' action={() => setConfig({})} />
                            </ButtonGroup>
                            <ButtonGroup>
                                <ButtonConfig icon='fas fa-backward' action={() => setConfig({})} />
                                <ButtonConfig icon='fas fa-forward' action={() => setConfig({})} />
                            </ButtonGroup>
                        </ButtonGroup>
                    </RowConfig>
                    <TitleCard>GENERAL</TitleCard>
                </CardConfig>
            </RowConfig >
            <CardConfig marginNull width="131">
                <RowConfig >
                    <ButtonGroup>
                        <ButtonConfig icon='far fa-crop-alt' active={edit === 'crop'} action={() => setConfig({ edit: 'crop' })} />
                        <ButtonConfig icon='fas fa-brush' active={edit === 'color'} action={() => setConfig({ edit: 'color' })} />
                        <ButtonConfig icon='fas fa-adjust' active={edit === 'contrast'} action={() => setConfig({ edit: 'contrast' })} />
                        <ButtonConfig icon='fas fa-filter' active={edit === 'filter'} action={() => setConfig({ edit: 'filter' })} />
                    </ButtonGroup>
                </RowConfig>
                <TitleCard>OPCIONES DE EDICION</TitleCard>
            </CardConfig>
            <Interfaz {...{ config, setConfig, crop, draw }} />
        </Row>
    </Card>
}
interface PropsFiltro {
    setData: (d: number) => void
    value: number
    max: number
    min: number
    sal: number
    step: number
    orientation?: 'vertical'
}
export function Filtro({ setData, value, max, min, sal, step, orientation }: PropsFiltro) {
    const slider = orientation ?
        { height: 'calc(100% - 20px)', margin: '5px 0 10px 0' } :
        { width: 'calc(100% - 20px)', margin: '0 10px 0 10px' };
    const div = orientation ?
        { width: 32, height: 'calc(100% - 48px)' } :
        { height: 24, width: 'calc(100% - 64px)' };
    return <div style={{ margin: '0 0 0 0', padding: '0 0 0 0', width: 32 }}>
        <ButtonConfig icon='fas fa-search-plus' action={() => setData(value + sal)} />
        <div className='d-flex justify-content-center' style={div}>
            <Slider style={slider} {...{ value, max, min, orientation, step }}
                onChange={(e: any, d: any) => { setData(d) }} />
        </div>
        <ButtonConfig icon='fas fa-search-minus' action={() => setData(value - sal)} />
    </div>
}
function Guardar({ guardar }: { guardar: () => void }) {
    const CN = css`
    color: #28a745;width: 55px;padding: .3rem .3rem .3rem .3rem;border-color: #28a745;
    &:hover{color: white;background-color: #28a745;}`;
    return <Button variant='default' onClick={guardar} className={CN} >
        <i className="fa fa-save fa-2x"></i>
    </Button>
}
interface InterfaceProps {
    config: State;
    setConfig: (d: any) => void,
    crop: () => void
    draw: () => void
}
function Interfaz({ config, setConfig, crop, draw }: InterfaceProps) {
    const { edit } = config;
    switch (edit) {
        case 'crop':
            return <CropEdit setData={(rotate) => setConfig({ rotate })}
                value={config.rotate} onCrop={crop} />
            break;
        case 'color':
            return <ColorSeting {...{ config, setConfig }} />
            break;
        default: return <></>
    }

}
export { Zoom };