export interface Crop { x: number, y: number, width: number, height: number }
type cropimage = (src: string, pixelCrop: Crop, rotation?: number, flip?: { horizontal: boolean, vertical: boolean }) => Promise<string>;
type RGB = [number, number, number];//[r,g,b]
type HSL = [number, number, number];//[h,s,l]
export interface MatrizReturn {
    rgb: RGB[][]
    hsl: HSL[][]
    red: number
    green: number
    blue: number
    hue: number
    saturate: number
    ling: number
    width: number
    height: number
}
export const MatrizNull: MatrizReturn = {
    rgb: [[[0, 0, 0]]],
    hsl: [[[0, 0, 0]]],
    red: 0,
    green: 0,
    blue: 0,
    hue: 0,
    saturate: 0,
    ling: 0,
    width: 0,
    height: 0
}
export class ImageManager {
    url = '';
    createImage: (url: string) => Promise<HTMLImageElement> = (url: string) =>
        new Promise((resolve, reject) => {
            const image = new Image()
            image.addEventListener('load', () => resolve(image))
            image.addEventListener('error', (error) => reject(error))
            image.setAttribute('crossOrigin', 'anonymous') // needed to avoid cross-origin issues on CodeSandbox
            image.src = url
        })
    drawImageMatriz: (matriz: RGB[][]) => Promise<string> = (matriz: RGB[][]) =>
        new Promise(async (resolve, reject) => {
            const canvas = document.getElementById('canvaImage') as HTMLCanvasElement;
            const ctx = canvas.getContext('2d');
            if (!ctx) return null
            if (ctx) {
                const width = matriz.length;
                const height = matriz[0].length;
                canvas.width = width;
                canvas.height = height;
                const data = new Uint8ClampedArray(4 * width * height);
                const idata = new ImageData(data, width, height);
                for (let x = 0; x < width; x++) {
                    for (let y = 0; y < height; y++) {
                        const i = y * (width * 4) + x * 4;
                        const [r, g, b] = matriz[x][y];
                        data[i] = r; data[i + 1] = g; data[i + 2] = b; data[i + 3] = 255;
                    }
                }
                idata.data.set(data);
                ctx.putImageData(idata, 0, 0);
                return canvas.toBlob(
                    (blob) => {
                        if (!blob) {
                            //reject(new Error('Canvas is empty'));
                            console.error('Canvas is empty');
                            return;
                        }
                        window.URL.revokeObjectURL(this.url);
                        this.url = window.URL.createObjectURL(blob);
                        resolve(this.url);
                    },
                    'image/jpeg',
                    1
                );
            }
        });
    getMatrisToString: (src: string) => Promise<MatrizReturn> = (src) =>
        new Promise(async (resolve, reject) => {
            const img = await this.createImage(src);
            const canvas = document.getElementById('canvaImage') as HTMLCanvasElement;
            canvas.width = img.width;
            canvas.height = img.height;
            const ctx = canvas.getContext('2d');
            if (!ctx) return null
            if (ctx) {
                const { width, height } = img;
                const div = width * height;
                ctx.drawImage(img, 0, 0)
                const { data } = ctx.getImageData(0, 0, width, height);
                const rgb: RGB[][] = [], hsl: HSL[][] = [];
                let red = 0, green = 0, blue = 0, hue = 0, saturate = 0, ling = 0;
                for (let x = 0; x <= width; x++) {
                    rgb[x] = []; hsl[x] = [];
                    for (let y = 0; y <= height; y++) {
                        const i = y * (width * 4) + x * 4;
                        const r = data[i], g = data[i + 1], b = data[i + 3];
                        const [h, s, l] = this.convertToHsl([r, g, b] as RGB);
                        red += r; green += g; blue += b; hue += h; saturate += s; ling += l;
                    }
                }
                red = Math.ceil(red / div);
                green = Math.ceil(green / div);
                blue = Math.ceil(blue / div);
                hue = Math.ceil(hue / div);
                saturate = Math.ceil(saturate / div);
                ling = Math.ceil(ling / div);
                resolve({ rgb, hsl, red, green, blue, hue, saturate, ling, width, height })
            }
            resolve(MatrizNull);
        })
    cropImage: cropimage = async (src: string, pixelCrop: Crop, rotation: number = 0, flip = { horizontal: false, vertical: false }) =>
        new Promise(async (resolve, reject) => {
            const img = await this.createImage(src);
            const canvas = document.createElement('canvas');
            const ctx = canvas.getContext('2d');
            const rotRad = this.getRadianAngle(rotation);
            if (!ctx) return null;
            const { width: bBoxWidth, height: bBoxHeight } = this.rotateSize(
                img.width,
                img.height,
                rotation
            );
            canvas.width = bBoxWidth;
            canvas.height = bBoxHeight;
            ctx.translate(bBoxWidth / 2, bBoxHeight / 2);
            ctx.rotate(rotRad);
            ctx.scale(flip.horizontal ? -1 : 1, flip.vertical ? -1 : 1);
            ctx.translate(-img.width / 2, -img.height / 2);
            ctx.drawImage(img, 0, 0);
            const data = ctx.getImageData(
                pixelCrop.x,
                pixelCrop.y,
                pixelCrop.width,
                pixelCrop.height
            )
            canvas.width = pixelCrop.width
            canvas.height = pixelCrop.height
            ctx.putImageData(data, 0, 0);
            resolve(canvas.toDataURL());
        })
    getRadianAngle(degreeValue: number) {
        return (degreeValue * Math.PI) / 180
    }
    rotateSize(width: number, height: number, rotation: number) {
        const rotRad = this.getRadianAngle(rotation)
        return {
            width: Math.abs(Math.cos(rotRad) * width) + Math.abs(Math.sin(rotRad) * height),
            height: Math.abs(Math.sin(rotRad) * width) + Math.abs(Math.cos(rotRad) * height),
        }
    }
    convertToHsl(rgbArr: RGB) {
        var r1 = Number(rgbArr[0]) / 255, g1 = Number(rgbArr[1]) / 255, b1 = Number(rgbArr[2]) / 255;
        var maxColor = Math.max(r1, g1, b1), minColor = Math.min(r1, g1, b1);
        var L = (maxColor + minColor) / 2, S = 0, H = 0;
        if (maxColor != minColor) {
            if (L < 0.5) {
                S = (maxColor - minColor) / (maxColor + minColor);
            } else {
                S = (maxColor - minColor) / (2.0 - maxColor - minColor);
            }
            if (r1 == maxColor) {
                H = (g1 - b1) / (maxColor - minColor);
            } else if (g1 == maxColor) {
                H = 2.0 + (b1 - r1) / (maxColor - minColor);
            } else {
                H = 4.0 + (r1 - g1) / (maxColor - minColor);
            }
        }
        L = L * 100;
        S = S * 100;
        H = H * 60;
        if (H < 0) {
            H += 360;
        }
        return [H, L, S];
    }
}
var img = new ImageManager();
export default function getImageManager() {
    return img;
}


